.card {
    width: auto;
    height: auto;
}

.card-default {
}

.card-darken {
    opacity: 0.4;
    filter: alpha(opacity=40);
}

.class-icons {
    display: flex;
    justify-content: center;
}

#cards {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-evenly;
    align-content: center;
}

#cards div {
    min-height: 30%;
    display: flex;
    justify-content: center;
}

/* #016E01 */
.hunter {
    background-color: #016E01
}

.mage {
    background-color: #0092AB
}

.paladin {
    background-color: #AA8F00
}